var Oz2Gram = 28.34952;
var Gram2Oz = 0.03527396195;
var Output = document.getElementById("output");
document.getElementById("Gram2Ounces").addEventListener("click", function () { return grams2Ounces(); });
document.getElementById("Ounces2Gram").addEventListener("click", function () { return ounces2Gram(); });
function ConvertGramToOunces() {
    var inputWeight = Number(document.getElementById("numberTextinput").value);
    return inputWeight * Gram2Oz;
}
function ConvertOuncesToGram() {
    var inputWeight = Number(document.getElementById("numberTextinput").value);
    return inputWeight * Oz2Gram;
}
function grams2Ounces() {
    var result = ConvertGramToOunces();
    if (result > 0) {
        Output.innerHTML = result.toString() + " Ounces";
    }
}
function ounces2Gram() {
    var result = ConvertOuncesToGram();
    if (result > 0) {
        Output.innerHTML = result.toString() + " Grams";
    }
}
